.navbar {
  height: 50px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 1.2rem;
  position: sticky;
  top: 0;
  z-index: 999;
  background-color:rgba(0, 0, 0, 0.3) ;
  border-radius: 3px;
}

.navbar-container {
  display: flex;
  opacity: 0.5;
  justify-content: center;
  align-items: center;
  height: 50px;
  max-width: 1500px;
}

.navbar-logo {
  color: #FFF;
  float: left;
  margin: 30px;
  cursor: pointer;
  text-decoration: none;
  align-items: center;
  font-size: 1.1rem;
  font-weight: bold;
  text-shadow: 3px 3px rgb(255, 255, 255, 0.2);
  font-family: 'IBM Plex Mono', monospace;
}
.navbar-logo-svg{
  background-color: #FFF;
}

.nav-menu {
  display: grid;
  grid-template-columns: repeat(4, auto);
  grid-gap: 20px 0px;
  list-style: none;
  text-align: center;
  width: 60vw;
  justify-content: end;
  margin-right: 30px;
}

.nav-links {
  color: #FFF;
  display: flex;
  align-items: center;
  text-shadow: #FFF 15px;
  font-size: 1rem;
  font-weight: bold;
  font-family: 'IBM Plex Mono', monospace;
  text-decoration: none;
  padding: 0rem 2rem;
  height: 50px;
  cursor: pointer;
}

.nav-links:hover {
  color: rgba(0, 255, 13);
  transition: all 0.2s ease-in-out;
}

.fa-bars {
  color: #fff;
}

.menu-icon {
  display: none;
}

@media screen and (max-width: 960px) {
  .NavbarItems {
    position: relative;
  }

  .nav-menu {
    display: flex;
    flex-direction: column;
    width: 91%;
    height: 90vh;
    position: absolute;
    top: 70px;
    left: -100%;
    padding-top: 15px;
    padding-bottom: 15px;
    opacity: 1;
    transition: all 0.7s ease;
  }

  .nav-menu.active {
    background: #242222;
    border-radius: 2px;
    left: 0;
    opacity: 1;
    transition: all 0.5s ease;
    z-index: 1;
  }

  .nav-links {
    text-align: center;
    padding-left: 2rem;
    padding-right: 2rem;
    padding-top: 1rem;
    padding-bottom: 1.5rem;
    width: 70%;
    display: table;
    transition: all 0.2s ease-in;
  }

  .nav-links:hover {
    color: rgba(0, 255, 13);
    transition: all 0.2s ease-out;
  }

  .navbar-logo {
    position: absolute;
    top: 0;
    left: 0;
    margin: 25px;
    transform: translate(25%, 50%);
  }

  .menu-icon {
    display: block;
    position: absolute;
    top: 0;
    right: 0;
    margin: 10px;
    transform: translate(-100%, 60%);
    font-size: 1.8rem;
    cursor: pointer;
  }
  .navbar {
    height: 85px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 1.2rem;
    position: sticky;
    top: 0;
    z-index: 999;
    background-color:rgba(0, 0, 0, 0.3) ;
    border-radius: 3px;
  }

  .fa-times {
    color: #fff;
    font-size: 2rem;
  }
}