.about-me-heading{
    color: rgb(255, 255, 255);
    text-align: left;
    font-size: 60px;
    margin-left: 18%;
    font-weight: 100;
    font-family: 'Play', sans-serif;
}
.about-me-description{
    margin: 2%;
    margin-left: 5%;
    font-size: 20px;
    float: left;
    font-family: 'Cairo', sans-serif;
    width: 50%;
}
p{
    color: #ffffff;
}
.about-container{
    width: 100%;
    overflow: hidden;
}
.about-inner-container{
    margin-top: 5%;
    padding: 1rem;
    position: relative;
    display: block;
    background-image: linear-gradient(90deg, #424647 , #414345);
    height: 80vh;
    width: 100vw;
    top: 0;
    left: 0;
    overflow: hidden;
    margin-left: 8%;
}
.about-me-image{
    width: 100%;
    margin-top: -2%;
    height: auto;
    width: auto;
    height: 100%;
    max-height: 50vh;
    max-width: 50vw;
    border-radius: 50%;
    transition: all 0.5s ease-out;
    -moz-transition: all 0.5s ease-out;
    -o-transition: all 0.5s ease-out;
    box-shadow: -10px 25px 50px rgba(0, 0, 0, 0.3);
}
.about-me-image:hover{
    margin-top: 5px;
    transition: all 0.5s ease-out;
    box-shadow: 20px 50px 100px rgba(0, 0, 0, 0.8);
}

.resume-button{
    border: 1px solid white !important;;
    background-color: transparent;
    font-size: 20px;
    color: white;
    border-radius: 2px;
    padding: 2px 5px 2px 5px;
    margin-top: 10%;
    margin-left: 40%;
    border-radius: 5px;
    transition: all 0.5s ease-in;
    text-decoration: none;
}

.resume-button:hover{
    box-shadow: inset 0 125px 0 0 #ffffff;
    cursor: pointer;
    color: #1b1a18;
    transition: all 1s ease-out;
}
@media screen and (max-width: 960px) {
    .about-inner-container{
        height: 100vh;
        width: 100vw;
        margin-left: 0%;
    }
    .about-me-description{
        width: 70%;
        margin-bottom: 5%;
    }
    .about-me-image{
        visibility: hidden;
    }
    .about-me-heading{
        margin-left: 10%;
    }
    
}
@media screen and (max-width: 480px) {
    .about-inner-container{
        height: 120vh;
        width: 100vw;
        margin-left: 0%;
    }
    .about-me-description{
        width: 70%;
        margin-bottom: 5%;
    }
    .about-me-image{
        visibility: hidden;
    }
    .about-me-heading{
        margin-left: 10%;
    }
    
}