.projects-container{
    width: 100%;
    overflow: hidden;
}
.projects-inner-container{
    margin-top: 5%;
    padding: 1rem;
    position: relative;
    display: block;
    background-image: linear-gradient(90deg, #424647 , #414345);
    height: 100%;
    width: 100vw;
    top: 0;
    left: 0;
    overflow: hidden;
    margin-left: 8%;
    transition: all 0.5s ;
}
.cards{
    display: grid;
    grid-template-columns: repeat(2, auto);
    grid-gap: 5vh 5vw;
    list-style: none;
    text-align:center;
    width: 100vw;
    margin-left: -7%;
    justify-content:center;
    margin-top: 3%;
    margin-bottom: 3%;
    position:relative;
}
.projects-heading{
    color: rgb(255, 255, 255);
    text-align: left;
    font-size: 60px;
    margin-left: 18%;
    font-weight: 100;
    font-family: 'Play', sans-serif;
}
.card{
    width: 35vw;
    background-image: linear-gradient(90deg, #4d5152 , #414141);
    transition: all 0.5s ;
    box-shadow: -10px 25px 10px rgba(0, 0, 0, 0.3);
    top:0;
    position:relative;
    padding: 20px;
    transition: all 0.5s ;
}
.card:hover{
    transition: all 0.5s ;
    top: 5px;
    box-shadow: 20px 50px 100px rgba(0, 0, 0, 0.8);
}
.card-heading{
    font-size: 25px;
    float: left;
    margin: 0%;
    color: #ffffff;
    font-family: 'Changa', sans-serif;
}
.card-text{
    font-size: 15px;
    float: right;
    font-family: 'Cairo', sans-serif;
    position: relative;
}
.card-languages{
    font-size: 12px;
    text-align: center;
    float: left;
    font-family: 'Montserrat Alternates', sans-serif;
    margin-left: 5%;
    margin-right: 5%;
    word-spacing: 10px;
}
.languages-sub{
    font-weight: bold;
}

.github-button{
    border: 1px solid white !important;;
    background-color: transparent;
    font-size: 20px;
    font-family: 'Changa', sans-serif;;
    float: right;
    color: white;
    border-radius: 2px;
    padding-left: 10px;
    padding-right: 10px;
    margin-top: 5%;
    border-radius: 5px;
    transition: all 0.5s ease-in;
}

.github-button:hover{
    box-shadow: inset 0 125px 0 0 #000000;
    cursor: pointer;
    color: #ffffff;
    transition: all 1s ease-out;
}
@media screen and (max-width: 960px) {
    .projects-inner-container{
        width: 100vw;
        margin-left: 0%;
    }
    .cards{
        grid-template-columns: repeat(1, auto);
        margin-left: -15%;
        margin-bottom: 10%;
    }
    .card{
        width: 50vw;
    }
}