.skills-heading{
    color: rgb(255, 255, 255);
    text-align: left;
    font-size: 60px;
    margin-left: 18%;
    font-weight: 100;
    font-family: 'Play', sans-serif;
}
.skills-container{
    width: 100%;
    overflow: hidden;
    display: grid;
}
.skills{
    display: grid;
    grid-template-columns: repeat(5, auto);
    grid-gap: 55px 55px;
    list-style: none;
    text-align: center;
    width: 100vw;
    margin-left: -5%;
    justify-content:center;
    margin-top: 3%;
    margin-bottom: 3%;
}
.skill-card{
    width: 150px;
    height: 150px;
    background-image: linear-gradient(90deg, #424647 , #414345);
    transition: all 0.5s ;
    box-shadow: -10px 25px 10px rgba(0, 0, 0, 0.3);
    top:0;
    position:relative;
}
.skill-card:hover{
    transition: all 0.5s ;
    top: 5px;
    box-shadow: 20px 50px 100px rgba(0, 0, 0, 0.8);
}
.skill-img{
    padding: 15px;
    width: 120px;
    height: 120px;
    transition: all 0.5s ;
}
.skill-img:hover{
    width: 160px;
    height: 160px;
    padding: 0px;
    transition: all 0.5s ;
}
.skills-inner-container{
    margin-top: 5%;
    padding: 1rem;
    position: relative;
    display: block;
    background-image: linear-gradient(90deg, #424647 , #414345);
    width: 100vw;
    height: 80vh;
    top: 0;
    right: 0;
    overflow: hidden;
    margin-right: 10%;
}

@media screen and (max-width: 1050px) {
    .skills-inner-container{
        height: 100%;
        width: 100vw;
        padding-bottom: 10%;
    }
    .skills-description{
        width: 70%;
    }
    .skills-image{
        visibility: hidden;
    }
    .skills{
        grid-template-columns: repeat(2, auto);
        margin-left: -15%;
    }
}