.contact-me-heading{
    color: rgb(255, 255, 255);
    text-align: left;
    font-size: 60px;
    margin-left: 10%;
    font-weight: 100;
    font-family: 'Play', sans-serif;
}
.contact-container{
    width: 100%;
    overflow: hidden;
}
.contact-inner-container{
    text-align: left;
    margin-top: 5%;
    padding-top: 1rem;
    padding-bottom: 1rem;
    padding-left: 10rem;
    padding-right: 10rem;
    position: relative;
    display: block;
    background-image: linear-gradient(90deg, #424647 , #414345);
    top: 0;
    left: 0;
    overflow: hidden;
}
.contact-text{
    font-size: 20px;
    text-align: left;
    font-family: 'Cairo', sans-serif;
    width: 46vw;
}
.form{
    text-decoration: none;
    height: 50vh;
    
}
.form-name,
.form-email,
.form-subject,
.form-message{
    background-color:#424647;
    font-family: 'Cairo', sans-serif;
    font-size: 15px;
    color: #ffffff;
    margin-bottom: 2%;
    border: 0.1px solid rgb(88, 88, 88);
    border-bottom: 2px solid rgb(0, 0, 0);
    overflow-wrap: break-word;
    word-break: break-all;
    transition: all 0.5s ;
}
.form-email{
    width: 35%;
    margin-left: 1%;
}
.form-name{
    width: 25%;
}
.form-message{
    width: 61.5%;
    height: 20vh;
    resize: none;
    margin-right: 100%;
}
.form-subject{
    width: 61.5%;
}
.form-email:hover{
    border-bottom: 2px solid rgb(255, 255, 255);
    transition: all 0.5s ;
}
.form-name:hover{
    border-bottom: 2px solid rgb(255, 255, 255);
    transition: all 0.5s ;
}
.form-message:hover{
    border-bottom: 2px solid rgb(255, 255, 255);
    transition: all 0.5s ;
}
.form-subject:hover{
    border-bottom: 2px solid rgb(255, 255, 255);
    transition: all 0.5s ;
}
.send-email-button{
    border: 1px solid white !important;
    background-color: transparent;
    font-size: 15px;
    color: white;
    border-radius: 5px;
    border-bottom: 2px solid #ffffff;
    font-family: 'Cairo', sans-serif;
    transition: all 0.5s ease-in;
    padding-left: 10px;
    padding-right: 10px;
    position: relative;
    margin-top: -10px;
}
.send-email-button:hover{
    box-shadow: inset 0 125px 0 0 #ffffff;
    cursor: pointer;
    color: #1b1a18;
    transition: all 1s ease-out;
}
.error-message{
    color: red;
    font-size: 15px;
    font-family: 'Cairo', sans-serif;
    margin-left: 0%;
    text-align: center;
    padding: 0px;
    background-color: black;
    border: 2px solid black;
    z-index: -1;
}
.successful-message{
    color: rgb(0, 219, 18);
    font-size: 15px;
    font-family: 'Cairo', sans-serif;
    margin-left: 0%;
    text-align: center;
    padding: 0px;
    background-color: black;
    border: 2px solid black;
    z-index: -1;
}
.unsuccessful-message{
    color: rgb(255, 0, 0);
    font-size: 15px;
    font-family: 'Cairo', sans-serif;
    margin-left: 0%;
    text-align: center;
    padding: 0px;
    background-color: black;
    border: 2px solid black;
    z-index: -1;
}
.small-logo-svg{
    filter: invert(80%) sepia(0%) saturate(2476%) hue-rotate(86deg) brightness(118%) contrast(119%);
    position:absolute;
    z-index:0;
    margin-left: 53%;
    margin-top: 0%;
    transition: all 3s ease-out;
    border-radius: 50%;
    box-shadow: 20px 50px 100px rgba(0, 0, 0, 0.4);
}
@media screen and (max-width: 1050px) {
    .contact-inner-container{
        text-align: left;
        margin: 5% 5% 5% 0%;
        padding-top: 1rem;
        padding-bottom: 1rem;
        padding-left: 1rem;
        padding-right: 1rem;
    }
    .contact-text{
        font-size: 20px;
        text-align: left;
        font-family: 'Cairo', sans-serif;
        width: 80vw;
    }
    .form-email{
        width: 55.8%;
    }
    .form-name{
        width: 40%;
    }
    .form-message{
        width: 100%;
        height: 20vh;
        resize: none;
    }
    .form-subject{
        width: 100%;
    }
    .small-logo-svg{
        visibility: hidden;
    }
}
@media screen and (max-width: 480px) {
    .form-email{
        width: 55%;
    }
}