.main-container{
    width: 100%;
    overflow: hidden;
    margin: 0;
    padding: 0;
}
.inner-container{
    display: block;
    background-image: linear-gradient(90deg, #424647 , #414345);
    position: relative;
    height: 100vh;
    width: 100vw;
    top: 0;
    left: 0;
    overflow: hidden;
}
.small-intro{
    font-size: 30px;
    font-family: 'Inconsolata', monospace;
    margin-left: 8%;
    margin-top: 60px;
    position:absolute;
    z-index:1;
    border: 2px 2px rgb(0, 255, 13, 0.4) ;
}

.intro-mark{
    background-color: rgb(44, 43, 43 , 0.3);
    color: white;
    border-radius: 10px;
}

.contact-me-button{
    border: 1px solid white !important;;
    background-color: transparent;
    font-size: 20px;
    color: white;
    margin-left: 25%;
    border-radius: 2px;
    padding: 10px;
    margin-top: 0px;
    font-family: Arial, Helvetica, sans-serif;
    transition: all 0.5s ease-in;
}


.contact-me-button:hover{
    box-shadow: inset 125px 0 0 0 #ffffff;
    cursor: pointer;
    color: #1b1a18;
    transition: all 0.5s ease-out;
}

.contact-me-button:active{
    cursor: pointer;
    color: #1b1a18;
    background-color: rgba(0, 255, 13, 0.5);
    transition: all 0.7s ease-out;
}

.logo-svg{
    filter: invert(48%) sepia(79%) saturate(2476%) hue-rotate(86deg) brightness(118%) contrast(119%);
    position:absolute;
    z-index:0;
    margin-left: 40%;
    margin-top: 4%;
    animation: logo-spin 100s ease-out infinite;
    transform-origin: center;
    transition: all 5s ease-out;
    border-radius: 50%;
    box-shadow: 20px 50px 100px rgba(9, 255, 0, 0.8);
}
.logo-svg:hover{
    filter: invert(48%) sepia(79%) saturate(2476%) hue-rotate(1000deg) brightness(118%) contrast(119%);
    transition: all 10s ease-out;
    box-shadow: 200px 100px 1000px rgba(9, 255, 0, 0.8);
}
.logo-svg:active{
    filter: invert(48%) sepia(79%) saturate(2476%) hue-rotate(2000deg) brightness(118%) contrast(119%);
    transition: all 5s ease-out;
    box-shadow: 200px 500px 1000px rgba(9, 255, 0, 0.8);
}

.contact-logos-list{
    margin-left: 9%;
    margin-top: 490px;
    position:absolute;
    z-index:1;
    display: grid;
    grid-template-columns: repeat(4, auto);
    grid-gap: 20px 0px;
    list-style: none;
    text-align: center;
    justify-content: end;
}

.contact-logos-instagram,
.contact-logos-twitter,
.contact-logos-github,
.contact-logos-linkedin{
    float: left;
    margin: 20px;
    cursor: pointer;
    text-decoration: none;
    align-items: center;
    font-size: 3rem;
    font-weight: bold;
    transition: all 0.5s ease-in;
    text-shadow:1px 1px rgb(255, 255, 255, 0.2);
}
.contact-logos-instagram{
    color: transparent;
    -webkit-background-clip: text;
    background-clip: text;
    background-image:linear-gradient(45deg, #f09433 0%, #e6683c 25%, #dc2743 50%, #cc2366 75%, #bc1888 100%);
}
.contact-logos-github{
    color:rgba(0, 0, 0);
}
.contact-logos-twitter{
    color:#1DA1F2;
}
.contact-logos-linkedin{
    color:#0077b5;
}
.contact-logos-instagram:hover{
    color:rgb(255, 255, 255)
    ;
    transition: all 0.5s ease-out;
}
.contact-logos-github:hover{
    color:rgb(255, 255, 255)
    ;
    transition: all 0.5s ease-out;
}
.contact-logos-twitter:hover{
    color:rgb(255, 255, 255)
    ;
    transition: all 0.5s ease-out;
}
.contact-logos-linkedin:hover{
    color:rgb(255, 255, 255);
    transition: all 0.5s ease-out;
}

@keyframes logo-spin{
    from{
        transform: rotateZ(0deg);
    }
    to{
        transform: rotateZ(360deg);
    }
}
@-webkit-keyframes logo-spin{
    from{
        transform: rotateZ(0deg);
    }
    to{
        transform: rotateZ(360deg);
    }
}
@media screen and (max-width: 960px) {
    .contact-logos-list{
        margin-left: 0;
        margin-top: 420px;
    }
    .small-intro{
        margin-left: 8%;
        margin-top: 0%;
        font-size: 23px;
    }
}